html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

.page {
    height: 100%;
    font-family: var(--font-family, sans-serif);
    -webkit-text-size-adjust: 100%;
    color: var(--text-color);
}

.page__body {
    margin: 0;
    min-width: 320px;
    min-height: 100%;
    display: grid;
    grid-template-columns: auto minmax(290px, 1440px) auto;
    font-size: 16px;

    @include tablet {
        grid-template-columns: minmax(290px, 1440px);
    }
}

main {
    flex: auto;

    height: 100vh;
}

img {
    height: auto;
    max-width: 100%;
    object-fit: cover;
}

picture {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & source,
    & img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

a {
    text-decoration: none;
}

.site-container {
    overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
    display: none !important;
    /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
    border: none;
    padding: 0;
    background: transparent;
    cursor: pointer;
}

.btn {
    display: inline-flex;
    padding: 14px 32px;
    border-radius: 50px;
    transition: 0.3s;

    &-primary {
        background-color: var(--accent-color);
        color: var(--white-color);
    }
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.input-reset {
    -webkit-appearance: none;
    appearance: none;
    border: none;
    border-radius: 0;
    background-color: #fff;

    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration {
        display: none;
    }
}

.visually-hidden {
    position: absolute;
    overflow: hidden;
    margin: -1px;
    border: 0;
    padding: 0;
    width: 1px;
    height: 1px;
    clip: rect(0 0 0 0);
}

section {
    height: 100vh;
}

.container {
    width: 100%;
    max-width: var(--container-width);
    height: 100%;
    margin: 0 auto;
    padding: 0 var(--container-offset);
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.centered {
    text-align: center;
}

.dis-scroll {
    position: fixed;
    left: 0;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    overscroll-behavior: none;
}

.page--ios .dis-scroll {
    position: relative;
}
